import React from 'react';

import {
  MessageBoxWrapper,
} from './styled';

export const MessageBox: React.FC<{
  icon: React.ReactNode;
  onAction?: () => void;
  content: string | React.ReactElement;
  actionTitle?: string;
  style?: React.CSSProperties;
}> = (props) => {
  const { icon, onAction, content, actionTitle, style } = props;
  return (
    <MessageBoxWrapper style={style}>
      {icon}
      <div className='wdn-msg-content'>{content}</div>
      {actionTitle && (
        <div
          className='wdn-msg-action'
          onClick={onAction}
          onMouseDown={e => e.nativeEvent.preventDefault()}
        >
          {actionTitle}
        </div>
      )}
    </MessageBoxWrapper>
  );
};